import React, { Component } from 'react'
import Abdur from '../../assets/Abdur-Rahman as-Sudais.png';
import Mishari from '../../assets/Mishari Rashid al-`Afasy.jpg';
import Saad from '../../assets/Saad al-Ghamdi.png';
import { Link } from 'react-router-dom';
import '../../css/Tokoh.css'


export default class Tokoh extends Component {

  constructor(props) {
    super(props)

    this.state = {
      tokoh: [
        {
          nama: "Mishari Rashid al-`Afasy",
          imgProfil: Mishari,
          lahir: "5 September 1976",
          profesi: "Qari, Imam, Hafiz",
          tempatLahir: "Kuwait"
        },
        {
          nama: "Abdur-Rahman as-Sudais",
          imgProfil: Abdur,
          lahir: "10 Februari 1960",
          profesi: "Imam",
          tempatLahir: "Riyadh, Arab Saudi"
        },
        {
          nama: "Saad al-Ghamdi",
          imgProfil: Saad,
          lahir: "1967",
          tempatLahir: "Dammam, Arab Saudi"
        }
      ]
    }
  }


  render() {
  
    let cards = this.state.tokoh.map((t, index) => {
      return (
        <Link to={"/MoslemApps/Murottal/Qari/" + t.nama} className="link" key={index} >
          <div className="card-tokoh">
            <img src={t.imgProfil} className="img-tokoh" alt="Gambar" />
            <div className="text-card">
              <label>{t.nama}</label>
            </div>
          </div>
        </Link>
      )
    })

    return (
      <div className="wrapping-card-tokoh">
        {cards}
      </div>
    )
  }
}
