import React, { Component } from 'react';
import Navbar from '../Navbar/Navbar';
import Search from './Search';
import Content from './Content';
import dataQuran from '../../assets/quran-completed.json';

export default class Quran extends Component {

  constructor(props) {
    super(props)

    this.state = {
      inputKeyword: '',
      darkMode: false,
      data: dataQuran
    };
    this.switchButton = this.switchingMode.bind(this);
    this.handleFilterTextChange = this.handleFilterWhenTextChange.bind(this);
  }

  switchingMode(mode) {
    this.setState({
      darkMode: mode
    });
  }

  handleFilterWhenTextChange(inputText) {
    this.setState({
      inputKeyword: inputText
    });
  }



  render() {
    let pathURL = this.props.match.path;
    return (
      <div>
        <Navbar pathURL={pathURL} switchButton={this.switchButton} modeWeb={this.state.darkMode} />
        <Search inputKeyword={this.state.inputKeyword} onFilterTextChange={this.handleFilterTextChange} />
        <Content data={this.state.data} inputKeyword={this.state.inputKeyword} modeWeb={this.state.darkMode} />
      </div>
    )
  }
}
