import React, { Component } from 'react';
import Quran from './Al-Quran/Home';
import Surah from './Al-Quran/Surah';
import TheTafsir from './Tafsir/Home';
import Tafsir from './Tafsir/Surah';
import Murottal from './Murottal/Home';
// import AudioMurottal from './Murottal/Surah';
import Qari from './Murottal/Qari';
import Recitations from './Murottal/Recitations';
import '../css/generals.css';
import { BrowserRouter, Route } from "react-router-dom";

export default class Web extends Component {

  render() {
    return (
      <div>
        <BrowserRouter>
          <Route path="/MoslemApps/" component={Quran} exact />
          <Route path="/MoslemApps/Al-Qur-an/surah/:id" exact component={Surah} />
          <Route path="/MoslemApps/Tafsir" exact component={TheTafsir} />
          <Route path="/MoslemApps/Tafsir/surah/:id" exact component={Tafsir} />
          <Route path="/MoslemApps/Murottal" exact component={Murottal} />
          {/* <Route path="/MoslemApps/Murottal/surah/:id" exact component={AudioMurottal} /> */}
          <Route path="/MoslemApps/Murottal/Qari/:id" exact component={Qari} />
          <Route path="/MoslemApps/Murottal/Qari/:id/:iQari/:iMurottal" exact component={Recitations} />
        </BrowserRouter>
      </div>
    )
  }
}