import React, { Component } from 'react';
import dataQuran from '../../assets/quran-completed.json';
import Navbar from '../Navbar/Navbar';
import '../../css/Surah.css';
import '../../css/Tafsir.css';

export default class Surah extends Component {

  constructor(props) {
    super(props)

    this.state = {
      data: dataQuran
    }
  }

  render() {
    const id = this.props.match.params.id;
    const dataQuran = this.state.data;
    let surahQuran = null;
    let tafsirAyat = null;
    let ayat = null;
    dataQuran.forEach(surah => {
      if (surah.name === id) {
        surahQuran = surah;
        tafsirAyat = surah.tafsir.id.kemenag.text;
      }
    })

    let rowsTafsir = [];
    for( let ayat in tafsirAyat ) {
      rowsTafsir.push(tafsirAyat[ayat]);
    }

    let title = 
      <div className="title-section">
        <div className="wrapping-title">
          <h3>{surahQuran.name} ({surahQuran.name_translations.ar})</h3>
          <label>{surahQuran.name_translations.id}</label>
        </div>
      </div>

    let bismillah =
      <div className="bismillah-section">
        <label className="bismillah">{surahQuran.bismillah[0].text}</label>
        <span className="translate">{surahQuran.bismillah[0].id}</span>
      </div>

    ayat = rowsTafsir.map((row, index) => {
      return (
        <div className="ayat" key={index}>
          <div className="ayat-text">
            <span className="number-ayyat">Ayat ke: {index + 1}</span>
          </div>
          <p className="tafsir">{row}</p>
        </div>
      )
    })

    let pathURL = this.props.match.path;

    return (
      <div>
        <Navbar pathURL={pathURL} />
        {title}
        {bismillah}
        <div className="ayyat-section">
          {ayat}
        </div>
      </div>
    )
  }
}
