import React, { Component } from 'react'
import '../../css/Search.css';

export default class Search extends Component {

  constructor(props) {
    super(props);
    this.whenTextChange = this.filterWhenTextChange.bind(this);
  }

  filterWhenTextChange(e) {
    this.props.onFilterTextChange(e.target.value);
  }

  render() {
    return (
      <div className="p20 flex-center">
        <div className="board-search">
          <label className="font1">Cari Surah</label>
          <input type="text" className="input-keyword" placeholder="Nama Surah..." value={this.props.inputKeyword} onChange={this.whenTextChange} />
        </div>
      </div>
    )
  }
}