import React, { Component } from 'react'
import Navbar from '../Navbar/Navbar';
import '../../css/Murottal.css';
import Abdur from '../../assets/Abdur-Rahman as-Sudais.png';
import Mishari from '../../assets/Mishari Rashid al-`Afasy.jpg';
import Saad from '../../assets/Saad al-Ghamdi.png';
import dataQuran from '../../assets/quran-completed.json';

export default class Recitations extends Component {

  constructor(props) {
    super(props)

    this.state = {
      data: dataQuran,
      indexMurottal: parseInt(this.props.match.params.iMurottal),
      indexQari: this.props.match.params.iQari,
      playingSong: false,
      valueVolume: 100,
      valueDuration: 0,
      audioDuration: 0,
      autoplay: false,
      mute: false,
      buttonTriangle: false,
      murottalData: [
        {
          img: Mishari,
          nama: "Mishari Rashid al-`Afasy"
        },
        {
          img: Abdur,
          nama: "Abdur-Rahman as-Sudais"
        },
        {
          img: Saad,
          nama: "Saad al-Ghamdi"
        }
      ]
    };
    this.url = this.state.data[this.state.indexMurottal].recitations[this.state.indexQari].audio_url;
    this.audio = new Audio(this.url);
  }

  componentWillUnmount() {
    this.audio.pause();
  }

  render() {
    const pathURL = this.props.match.path;
    const dataQuran = this.state.data;
    let playedSong = this.state.playingSong;

    // Play Button
    let justPlay = (playedSong) => {
      if (playedSong === true) {
        pauseSong();
      } else if (playedSong === false) {
        playSong();
      } else {
        console.log("ELSE");
      }
    }

    let pauseSong = () => {
      this.audio.pause();
      this.setState({
        playingSong: false
      });

      return;
    }

    let playSong = () => {
      this.audio.play();
      this.setState({
        playingSong: true
      });
      setInterval(rangeSlider, 300);
    }

    // Next Song 
    let nextSong = (iMurott, number = 1) => {
      if (iMurott < dataQuran.length - 1) {
        pauseSong();
        this.setState({
          indexMurottal: iMurott + 1
        });

        this.url = this.state.data[this.state.indexMurottal + number].recitations[this.state.indexQari].audio_url;
        this.audio = new Audio(this.url);
        playSong();
      } else {
        pauseSong();
        this.setState({
          indexMurottal: 0
        })
        this.url = this.state.data[0].recitations[this.state.indexQari].audio_url;
        this.audio = new Audio(this.url);
        playSong();
      }
    }

    // Previous Song
    let previousSong = (iMurott) => {
      if (iMurott > 0) {
        pauseSong();
        this.setState({
          indexMurottal: iMurott - 1
        });

        this.url = this.state.data[this.state.indexMurottal - 1].recitations[this.state.indexQari].audio_url;
        this.audio = new Audio(this.url);
        playSong();
      } else {
        pauseSong();
        this.setState({
          indexMurottal: this.state.data.length - 1
        })
        this.url = this.state.data[this.state.data.length - 1].recitations[this.state.indexQari].audio_url;
        this.audio = new Audio(this.url);
        playSong();
      }
    }

    // Control Speaker / Volume
    let volumeControls = () => {
      const recentVolume = document.querySelector('#volume').value;
      this.setState({
        valueVolume: recentVolume
      })
      this.audio.volume = recentVolume / 100;
    }

    // Mute Sound
    let muteSound = () => {
      if (this.state.mute === false) {
        this.setState({
          mute: true
        });
        this.audio.volume = 0;
      } else {
        this.setState({
          mute: false
        });
        this.audio.volume = this.state.valueVolume / 100;
      }
    }

    let setIconVolume = () => {
      if (this.state.valueVolume >= 70) {
        return "fas fa-volume-up";
      } else if (this.state.valueVolume >= 30) {
        return "fas fa-volume";
      } else if (this.state.valueVolume >= 1) {
        return "fas fa-volume-down";
      } else {
        return "fas fa-volume-off";
      }
    }


    // Auto Play Switch
    let autoPlaySwitch = () => {
      if (this.state.autoplay === false) {
        this.setState({
          autoplay: true
        })
      } else {
        this.setState({
          autoplay: false
        })
      }
    }

    // Duration Control
    let durationControl = () => {
      playSong();
      const slider = document.querySelector("#sliderDuration").value;
      let sliderPosition = this.audio.duration * (slider / 100);
      this.audio.currentTime = sliderPosition;
    }

    let rangeSlider = () => {
      // Update Slider Position
      if (!isNaN(this.audio.duration)) {
        this.setState({
          valueDuration: this.audio.currentTime * (100 / this.audio.duration)
        })
        // document.querySelector("#sliderDuration").value = this.state.valueDuration;
      }

      // Jika music Selesai
      if (this.audio.ended) {
        this.setState({
          playingSong: false
        });
        if (this.state.autoplay === true) {
          nextSong(this.state.indexMurottal, 0);
        }
      }
    }

    // Countdown Recitations 
    let countdown = Math.round(this.audio.currentTime);
    let jam = Math.floor((countdown / 60) / 60);
    if (((countdown / 60) / 60) < 10) {
      jam = "0" + Math.floor((countdown / 60) / 60);
    } else {
      jam = "" + Math.floor((countdown / 60) / 60);
    }
    let menit = 0;
    if (((countdown / 60) % 60) < 10) {
      menit = "0" + Math.floor(((countdown / 60) % 60));
    } else {
      menit = "" + Math.floor(((countdown / 60) % 60));
    }
    let detik = 0;
    if (countdown % 60 < 10) {
      detik = "0" + countdown % 60;
    } else {
      detik = "" + countdown % 60;
    }

    let countdownRecitations = 0;
    if (jam < 1) {
      countdownRecitations = menit + ": " + detik
    } else {
      countdownRecitations = jam + ": " + menit + ": " + detik;
    }

    // Duration Recitations

    let durations = Math.round(this.audio.duration);
    let jamDuration = Math.floor((durations / 60) / 60);
    if (((durations / 60) / 60) < 10) {
      jamDuration = "0" + Math.floor((durations / 60) / 60);
    } else {
      jamDuration = "" + Math.floor((durations / 60) / 60);
    }

    let menitDuration = 0;
    if (((durations / 60) % 60) < 10) {
      menitDuration = "0" + Math.floor(((durations / 60) % 60));
    } else {
      menitDuration = "" + Math.floor(((durations / 60) % 60));
    }

    let detikDuration = 0;
    if (durations % 60 < 10) {
      detikDuration = "0" + durations % 60;
    } else {
      detikDuration = "" + durations % 60;
    }

    let durationRecitations = 0;
    if (jamDuration < 1) {
      durationRecitations = menitDuration + ": " + detikDuration;
    } else {
      durationRecitations = jamDuration + ": " + menitDuration + ": " + detikDuration;
    }

    let clickTriangleButton = (key) => {
      this.setState({
        buttonTriangle: !key
      })
    }

    let removeBarVolume = () => {
      this.setState({
        buttonTriangle: false
      })
    }

    return (
      <div>
        <Navbar pathURL={pathURL} />
        <div className="main">
          {/* <div className="section-top"> */}
           
            <div className="section-top">
              <img src={this.state.murottalData[this.state.indexQari].img} className="thumb" alt="gambar" />
            </div>
            <div className="section-mid">
              <label>{this.state.data[this.state.indexMurottal].name}</label>
              <label>{this.props.match.params.id}</label>
            </div>
          {/* </div> */}
          <div className="section-bottom">
            <div className="duration-section">
              <p className="countdown">{countdownRecitations}</p>
              <input type="range" className="range-duration" id="sliderDuration" value={this.state.valueDuration} onChange={() => durationControl()} />
              <p className="durationReci">{isNaN(durations) ? "00:00" : durationRecitations}</p>
            </div>
            <div className="controls-section">
              <div className="bar-control">
                <div className="controls-recitation">
                  <button title="autoplay" onClick={() => autoPlaySwitch()} className={this.state.autoplay ? "btn-autoplay-true" : "btn-autoplay"}>
                    <i className="fas fa-repeat"></i>
                  </button>
                </div>
                <div className="button-section">
                  <button onClick={() => previousSong(this.state.indexMurottal)}>
                    <i className="fas fa-step-backward"></i>
                  </button>
                  <button onClick={() => justPlay(playedSong)}>
                    <i className={this.state.playingSong ? "fa fa-pause" : "fa fa-play"}></i>
                  </button>
                  <button onClick={() => nextSong(this.state.indexMurottal)}>
                    <i className="fas fa-step-forward"></i>
                  </button>
                </div>
                <div className="volume-section">
                  <button onClick={() => muteSound()}>
                    <i className={this.state.mute ? "fas fa-volume-mute" : setIconVolume()}></i>
                  </button>
                  <div className={this.state.buttonTriangle ? "bar-volume2" : "bar-volume"}>
                    <input type="range" min="0" max="100" value={this.state.valueVolume} onChange={() => volumeControls()} onTouchEnd={() => removeBarVolume()} onMouseUp={() => removeBarVolume()} id="volume" title={this.state.valueVolume} />
                    <p>{this.state.valueVolume}</p>
                  </div>
                  <button onClick={() => clickTriangleButton(this.state.buttonTriangle)}>
                    <i className="fas fa-caret-down"></i>
                  </button>
                </div>
                {/* <button className={this.state.theKey ? "okes" :"oke"} onMouseUp={() => action(this.state.theKey)}>OKE</button> */}
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}
