import React, { Component } from 'react';
import dataQuran from '../../assets/quran-completed.json';
import Abdur from '../../assets/Abdur-Rahman as-Sudais.png';
import Mishari from '../../assets/Mishari Rashid al-`Afasy.jpg';
import Saad from '../../assets/Saad al-Ghamdi.png';
import '../../css/Qari.css'
import Navbar from '../Navbar/Navbar';
import { Link } from 'react-router-dom';


export default class Qari extends Component {
  constructor(props) {
    super(props)

    this.state = {
      Quran: dataQuran,
      inputKeyword: "",
      tokoh: [
        {
          nama: "Mishari Rashid al-`Afasy",
          imgProfil: Mishari,
          lahir: "5 September 1976",
          profesi: "Qari, Imam, Hafiz",
          tempatLahir: "Kuwait"
        },
        {
          nama: "Abdur-Rahman as-Sudais",
          imgProfil: Abdur,
          lahir: "10 Februari 1960",
          profesi: "Imam",
          tempatLahir: "Riyadh, Arab Saudi"
        },
        {
          nama: "Saad al-Ghamdi",
          imgProfil: Saad,
          lahir: "1967",
          tempatLahir: "Dammam, Arab Saudi"
        }
      ]
    };

    this.typing = this.textWhenChange.bind(this);
  }

  textWhenChange(keyword) {
    this.setState({
      inputKeyword: keyword
    });
  }


  render() {
    const idQari = this.props.match.params.id;
    let imgProfil = null;
    let pictProfile = () => {
      this.state.tokoh.forEach((pict, index) => {
        if (idQari === pict.nama) {
          imgProfil = pict.imgProfil;
        }
      })
    }

    pictProfile();

    const Quran = this.state.Quran;
    let murottalQari = [];

    Quran.forEach((surah, index) => {
      surah.recitations.forEach((s) => {
        if (s.name === idQari) {
          murottalQari.push(s);
        }
      })
    })

    let cardMurottals = null;
    let notFound = null;
    cardMurottals = murottalQari.map((qari, index) => {
      let iQari = null;
      if(qari.name === "Mishari Rashid al-`Afasy") {
        iQari = 0;
      } else if(qari.name === "Abdur-Rahman as-Sudais") {
        iQari = 1;
      } else {
        iQari = 2;
      }

      return (
        <Link to={"/MoslemApps/Murottal/Qari/" + qari.name + "/" + iQari + "/" + index} className="link" key={index} state={{muro: 1}}>
          <div className="card-murottals">
            <label>{Quran[index].name}</label>
            <p>{qari.name}</p>
          </div>
        </Link>
      )
    })

    if (this.state.inputKeyword !== "") {
      cardMurottals = cardMurottals.map((row) => {
        let namaSurah = row.props.children.props.children[0].props.children;
        if (namaSurah.toLowerCase().indexOf(this.state.inputKeyword.toLowerCase()) !== -1) {
          return (
            row
          )
        } else {
          return false;
        }
      });
    }
 

    let pathURL = this.props.match.path;
    return (
      <div>
        <Navbar pathURL={pathURL} />
        <div className="jumbotron">
          <div className="jumbotron-info">
            <h3>{idQari}</h3>
            <div className="search-section">
              <label className="font1">Cari Surah</label>
              <input type="text" className="input-keyword" placeholder="Nama Surah..." value={this.state.inputKeyword} onChange={(e) => this.typing(e.target.value)} />
            </div>
          </div>
          <img src={imgProfil} alt={idQari} className="thumbQari" />
        </div>
        <div className="wrapping-card-murottals">
          {cardMurottals}
          {notFound}
        </div>
      </div>
    )
  }
}
