import React, { Component } from 'react'
import '../../css/Content.css';
import quran from '../../assets/quran.png';
import { Link } from 'react-router-dom';

export default class Content extends Component {

  render() {
    const surah = this.props.data;
    let keyword = this.props.inputKeyword;
    let cards = null;
    let cardsu = null;
    if (keyword === '') {
      cards = surah.map((m, index) => {
        return (
          <Link to={"/MoslemApps/Tafsir/surah/" + m.name} className="link" key={index} >
            <div className="card">
              <label>{m.name} (<strong className="arabic">{m.name_translations.ar}</strong>)</label>
              <div>{m.name_translations.id}</div>
              <div className="font2">{m.number_of_ayah} ayat</div>
            </div>
          </Link>
        )
      })
    } else if (keyword !== '') {
      let rows = [];
      surah.forEach(row => {
        if (row.name.toLowerCase().indexOf(keyword.toLowerCase()) !== -1) {
          rows.push(row);
        }
      })

      if (rows.length > 0) {
        cards = rows.map((m, index) => {
          return (
            <Link to={"/MoslemApps/Tafsir/surah/" + m.name} className="link" key={index}>
              <div className="card">
                <label>{m.name} (<strong className="arabic">{m.name_translations.ar}</strong>)</label>
                <div>{m.name_translations.id}</div>
                <div>{m.number_of_ayah} ayat</div>
              </div>
            </Link>
          )
        })
      }
      else {
        cardsu = <img src={quran} alt="Qur'an" className="quran-img" />
      }
    }

    return (
      <div>
        <div className="wrapper-card" >
          {cards}
        </div>
        <div className="not-found">
          <div className="wrapping-not-found">
            {cardsu}
          </div>
        </div>
      </div>
    )
  }
}